/* Sidebar */
.sidebar {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  background: white;
  width: 200px;
  padding: 0;
  color: #404040;
  font-size: .95rem;
  float: left; }
  .sidebar h2 {
    margin-top: 2rem;
    font-size: .9rem;
    color: #e2e2e2;
    text-transform: uppercase;
    color: white;
    vertical-align: baseline;
    padding: .75rem .5rem;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    font-family: "Poppins", Helvetica Neue, Helvetica, Arial, sans-serif;
    font-weight: 600; }
    .sidebar h2.one {
      background: #F6BF26; }
    .sidebar h2.two {
      background: #F7931E; }
    .sidebar h2.three {
      background: #C52062; }
    .sidebar h2.four {
      background: #00BFA5; }
    .sidebar h2.five {
      background: #049AE5; }
    .sidebar h2.six {
      background: #795da3; }
  .sidebar a {
    display: block;
    text-decoration: none;
    color: #404040;
    font-weight: 600;
    font-size: 1rem;
    padding: 0 1rem;
    margin-bottom: 3px; }
    .sidebar a:hover {
      color: #049AE5; }
  .sidebar .thumb {
    background: white;
    box-sizing: content-box;
    width: 30px;
    height: 30px;
    vertical-align: middle;
    padding: 5px;
    margin-right: 1rem; }
  .sidebar ul {
    margin: 0;
    padding: 0 1rem;
    list-style: none; }

/* Flex grid */
@media screen and (min-width: 1100px) {
  .flex-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
  .flex-column {
    width: 50%; } }

/* Code
  =============== */
.terminal {
  padding: 1rem 1.5rem;
  font-family: Menlo, "Roboto Mono", Courier New, monospace;
  border: 1px solid #d8d9da;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  font-size: .9rem; }

pre .terminal {
  font-size: .75rem; }

code[class*="language-"],
pre[class*="language-"],
pre code {
  color: #D5D5D5;
  font-family: Menlo, "Roboto Mono", Courier New, monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 1.6;
  font-weight: 400;
  tab-size: 2;
  hyphens: none;
  font-size: .9rem;
  border-radius: 4px;
  -webkit-font-smoothing: subpixel-antialiased; }

/* Code blocks */
pre code {
  display: block;
  font-size: .75rem;
  padding: 1rem;
  margin: 1.5rem 0;
  overflow: auto;
  border: 0px solid #3B3B3B;
  background-color: #3B3B3B; }

@media (min-width: 1200px) {
  pre code {
    padding: 2rem;
    margin: 2rem -15%;
    border-left: 20px solid #232323; } }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .2rem;
  padding-top: 1px;
  padding-bottom: 1px;
  background: #efefef;
  border: 0px solid #efefef;
  -webkit-font-smoothing: subpixel-antialiased; }

:not(pre) > code {
  font-size: 1rem;
  padding: .01rem .2rem;
  background: #efefef;
  font-family: Menlo, "Roboto Mono", Courier New, monospace;
  font-weight: 400;
  border-radius: 4px;
  color: #404040;
  -webkit-font-smoothing: subpixel-antialiased; }

code[class*=language-css],
code[class*=language-scss],
code[class*=language-less],
code[class*=language-sass] {
  color: #CC99CC; }

/* Syntax Highlighting */
.token.doctype {
  background: #000000;
  color: #fff; }

.token.comment,
.token.prolog,
.token.cdata {
  color: #A9A9A9; }

.token.namespace {
  opacity: .7; }

.token.string,
.token.attr-value {
  color: #A8D3A9; }

.token.punctuation {
  color: #D5D5D5; }

.token.operator {
  color: #cda869; }

.token.variable {
  color: #F78D8C; }

.token.property {
  color: #D5D5D5; }

.token.entity,
.token.url,
.token.symbol,
.token.number,
.token.boolean,
.token.constant,
.token.regex,
.token.inserted {
  color: #FCA369; }

.token.delimiter {
  background: #000;
  color: #fff;
  padding: 1px 2px; }

.token.atrule {
  color: #76D4D6; }

.token.keyword {
  color: #ffeead; }

.language-autohotkey .token.selector {
  color: #F7931E; }

.token.attr-name {
  color: #FFD479; }

.token.function {
  color: #76D4D6; }

.token.class-name {
  color: #D6ACD6; }

.token.deleted,
.language-autohotkey .token.tag {
  color: #af1c57; }

.token.tag {
  color: #78AAD6; }

.language-autohotkey .token.keyword {
  color: #ffeead; }

.token.selector {
  color: #FFD479; }

.token.italic {
  font-style: italic; }
